.intro {
    position: relative;
    overflow: hidden;
    z-index: 10;

    padding: 310px 0px 40px 0px;

    background: linear-gradient(0deg, rgba(7,29,104,1) 0%, rgba(11,75,236,1) 100%);

    // background: #0b47e1 url("../images/intro/bg-intro.jpg") center / cover no-repeat;

    &::before {
        content: "";
        position: absolute;
        top: 31%;
        left: 13%;
        background: url("../images/intro/bg-el-left.svg") 0 100% / cover no-repeat;
        width: 216px;
        height: 324px;
        max-width: 100%;
        z-index: -1;

        @media (max-width:$md2+px){
            top: 20%;
            opacity: .7;
        }
    }


    &::after {
        content: "";
        position: absolute;
        top: 27%;
        right: 0;
        background: url("../images/intro/bg-el-right.png") 0 100% / cover no-repeat;
        width: 316px;
        height: 682px;
        max-width: 100%;
        z-index: -1;

        @media (max-width: 425px){
            opacity: 0;
        }
    }

    @media (max-width:$md2+px){
        padding: 200px 0px 40px 0px;
    }

    &__content {
        text-align: center;
        color: #fff;
    }

    &__title {
        margin-bottom: 35px;
        @include font-size(40px);
        font-weight: 600;
    }

    &__text {
        margin: 0px auto 56px auto;
        max-width: 970px;

        font-family: "Ninito";
        line-height: 2;
    }
   
    &__btn {
        margin: 0px 0px 92px 0px;
        padding: 10px 48px;
        border-radius: 22px;
    }
}